.modal-open .bootstrap-timepicker-widget.dropdown-menu.open {
  z-index: $zindex-modal+1 !important;
}

.bootstrap-timepicker {
  position: relative;
  &.pull-right .bootstrap-timepicker-widget.dropdown-menu {
    left: auto;
    right: 0;
    &:before {
      left: auto;
      right: 12px;
    }
    &:after {
      left: auto;
      right: 13px;
    }
  }
  .add-on {
    cursor: pointer;
    i {
      display: inline-block;
      width: 16px;
      height: 16px;
    }
  }
}
.bootstrap-timepicker-widget.dropdown-menu {
  padding: 4px;
  &.open { display: inline-block; }
  &:before {
    border-bottom: 7px solid rgba(0, 0, 0, 0.2);
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    content: "";
    display: inline-block;
    position: absolute;
  }
  &:after {
    border-bottom: 6px solid #FFFFFF;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    content: "";
    display: inline-block;
    position: absolute;
  }
  &.timepicker-orient-left:before { left: 6px; }
  &.timepicker-orient-left:after { left: 7px; }
  &.timepicker-orient-right:before { right: 6px; }
  &.timepicker-orient-right:after { right: 7px; }
  &.timepicker-orient-top:before { top: -7px; }
  &.timepicker-orient-top:after { top: -6px; }
  &.timepicker-orient-bottom:before {
    bottom: -7px;
    border-bottom: 0;
    border-top: 7px solid #999;
  }
  &.timepicker-orient-bottom:after {
    bottom: -6px;
    border-bottom: 0;
    border-top: 6px solid #ffffff;
  }
  a.btn, input { border-radius: 4px; }
  table {
    width: 100%;
    margin: 0;
    td {
      text-align: center;
      height: 30px;
      margin: 0;
      padding: 2px;
      &:not(.separator) {
        min-width: 30px;
      }
      span { width: 100%; }
      a {
        border: 1px transparent solid;
        width: 100%;
        display: inline-block;
        margin: 0;
        padding: 8px 0;
        outline: 0;
        color: #333;
        i {
          margin-top: 2px;
          font-size: 18px;
        }
        &:hover {
          text-decoration: none;
          background-color: #eee;
          -webkit-border-radius: 4px;
          -moz-border-radius: 4px;
          border-radius: 4px;
          border-color: #ddd;
        }
      }
      input {
        width: 25px;
        margin: 0;
        text-align: center;
      }
    }
  }
  .modal-content { padding: 4px; }
}
@media (min-width: 767px) {
  .bootstrap-timepicker-widget.modal {
    width: 200px;
    margin-left: -100px;
  }
}
@media (max-width: 767px) {
  .bootstrap-timepicker {
    width: 100%;
    .dropdown-menu {
       width: 100%;
    }
  }
}
