/*
 * LOGIN/REGISTER/EXTR PAGE
 */

.display-image {
	margin-top: -60px; 
	margin-right: 20px;
}

.terms-body {
	max-height: 400px;
	overflow: auto;
}

#extra-page {
	background: $white;
	overflow: visible;
	
	.container {
	  border: none;
  	}

	#header-extra {
		margin: 0;
		height: 71px;
		border-bottom: 1px solid $gray-lighter !important;
		overflow: hidden;
		padding: 0 30px;
		background-clip: padding-box;
		border-width: 0;
		min-height: 28px;
		/*margin-bottom: 20px;*/
	  background: $white;

		#logo {
		  margin-top: 22px;
		  margin-left: 0px;

		img { width: 137px; }
	  }
	}

	#main {
		padding-top: 20px;
	}

	.hero {
		background-image: url("#{$base-url}/gradient/login.png");
		background-repeat: no-repeat;
		background-position: 0 137px;
		height: 360px;
		width: 100%;
		float: left;
	}

	h4.paragraph-header {
		color: #565656;
		font-size: 15px;
		font-weight: normal;
		line-height: 22px;
		margin-top: 15px;
		width: 270px;
	}

	h5.about-heading {
		color: #565656;
		font-size: 15px;
		font-weight: bold;
		line-height: 24px;
		margin: 0 0 5px;
	}

	.login-header-big {
		font-weight: 400;
	}

	.login-desc-box-l{
		min-height: 350px;
		width: 50%;
	}

	.login-app-icons {
		vertical-align: top;
		margin-top: 90px;
		width: 300px;
	}
}
  
#extra-page-header-space {
	float: right;
	text-align: right;
	display: block;
	vertical-align: middle;
	line-height: 71px;
	
	>:first-child {
	  font-size: 13px;
	  margin-right: 14px;
	  vertical-align: -3px;
	  font-weight: 400;
  }
  .btn {
	  font-weight: bold;
	  text-transform: uppercase;
  }
  > * {	display: inline-block; }
}	

@media (min-width: 768px) and (max-width: 880px) {
	#extra-page #header {
		padding: 0 5px !important;
	}
}

@media only screen and (min-width: 0px) and (max-width: 679px) {
	#extra-page #header {
		padding: 0 5px !important;
		#logo { margin-top: 22px; }
		#logo img { width: 135px; }
	}	
}
